<!--
 * @Description: 富文本使用示例 表格 单条数据详情
 * @Author: mhf
 * @Date: 2023-04-10 22:52:06
-->
<template>
  <div>
    <div class="main scroll-index-1">
      <div class="base--info">
        <!-- 基本信息头部 -->
        <div class="form-subhead">基本信息</div>
        <!-- 基本信息内容 -->
        <div class="base--info_main">
          <el-form
            ref="formRef"
            :inline="true"
            :model="formData"
            label-position="right"
            label-width="100px"
          >
            <el-form-item label="标题：" prop="name">
              <el-input v-model="formData.title" size="small" />
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="base--info">
        <div class="form-subhead">承诺内容</div>

        <div class="base--info_main">
          <el-form
            ref="formRef"
            :inline="true"
            :model="formData"
            label-position="right"
            label-width="100px"
          >
            <el-form-item label="内容：" prop="name">
              <div style="border: 1px solid #ccc; margin-top: 20px">
                <!-- 富文本编辑器 -->
                <Toolbar
                  style="border-bottom: 1px solid #ccc"
                  :editor="editor"
                  :defaultConfig="toolbarConfig"
                  :mode="mode"
                />
                <Editor
                  style="height: 500px; overflow-y: hidden"
                  v-model="html"
                  :defaultConfig="editorConfig"
                  :mode="mode"
                  @onCreated="onCreated"
                />
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
    <el-button type="primary" @click="save"> 保存</el-button>
    <el-button type="warning" @click="close()"> 返回</el-button>
  </div>
</template>

<script>
import { richTextDetail } from "@/api/allRequset";
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";

export default {
  name: "richTextExampleDetail",
  props: {
    rowId: {
      type: String,
    },
  },
  components: { Editor, Toolbar },
  data() {
    return {
      showWhatFlag: true, // 展示哪个页面：编辑或新增
      // 表单数据对象
      formData: {
        postId: "",
        title: "",
        content: "",
      },

      editor: null,
      toolbarConfig: {},
      mode: "default", // or 'simple'
      editorConfig: { placeholder: "" },
      html: "",
    };
  },
  methods: {
    /**
     * @Event 方法
     * @description: editor
     * */
    onCreated(editor) {
      this.editor = Object.seal(editor); // 一定要用 Object.seal() ，否则会报错
    },

    /**
     * @Event 方法
     * @description: 测试输出 formData 和 富文本框的内容
     * */
    save() {
      console.log(this.formData);
      console.log(this.html);
    },

    /**
     * @Event 方法
     * @description: 返回上一级
     * */
    close() {
      this.$parent.showOrHide();
    },

    /**
     * @Interface 接口
     * @description: 详情的接口
     * */
    richTextDetail() {
      richTextDetail(this.rowId).then((res) => {
        this.formData = res.data.data;
      });
    },
  },
  created() {},
  mounted() {
    this.richTextDetail();
    /**
     * @Event 方法
     * @description: 初始化 富文本框内容
     * */
    setTimeout(() => {
      if (this.formData.content) {
        this.html = this.formData.content;
      }
    }, 100);
  },
};
</script>

<style lang="scss" scoped></style>
