 * @Author: mhf
 * @Date: 2023-04-09 12:16:37
-->
<template>
  <div class="normal-padding">
    <div v-if="showRichTextDetail">
      <el-button type="warning" @click="close"> 返回</el-button>
      <el-table :data="tableData" style="width: 60%; margin-top: 20px" border>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column prop="postName" label="岗位名称" align="center">
        </el-table-column>
        <el-table-column prop="title" label="标题" align="center">
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="text" @click="showOrHide(scope.row)">
              编辑
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <richTextExampleDetail
      :row-id="rowId"
      v-if="!showRichTextDetail"
      ref="richTextExampleDetail"
    />
  </div>
</template>

<script>
import { safetyCommitmentConfig } from "@/api/allRequset";
import richTextExampleDetail from "@/views/richText/richTextExampleDetail";

export default {
  name: "richTextExample",
  data() {
    return {
      rowId: "", // 回显 富文本框 的数据拼接的id
      showRichTextDetail: true,
      tableData: [],
    };
  },
  components: { richTextExampleDetail },
  methods: {
    /**
     * @Event 方法
     * @description: 获取父组件的方法 回到首页
     * */
    close() {
      this.$parent.close();
    },

    /**
     * @Interface 接口
     * @description: 获取表格数据
     * */
    getTableData() {
      safetyCommitmentConfig().then((res) => {
        this.tableData = res.data.data;
      });
    },

    /**
     * @Event 方法
     * @description: 展示表格单条数据详情
     * */
    showOrHide(data) {
      console.log(data);
      if (data) {
        this.rowId = data.id;
      }
      this.showRichTextDetail = !this.showRichTextDetail;
    },
  },
  created() {
    this.getTableData();
  },
};
</script>

<style lang="scss" scoped></style>
